import React, { useCallback } from "react";
import Particles from "react-particles";
import type { Engine } from "tsparticles-engine";
import { loadFull } from "tsparticles";
import './App.css';
import particlesOptions from "./particles.json";
import { ISourceOptions } from "tsparticles-engine";
import treeLogo from "../src/assets/images/tree.png";
import ravenLogo from "../src/assets/images/raven-logo.png";
import arrow from  "../src/assets/images/arrow.svg";
import profile from  "../src/assets/images/raven-profile.jpg";
import resume from "../src/assets/files/ravenwei_resume.pdf";
import Projects from "./Projects";
import Welcome from "./Welcome";

function App() {
    const particlesInit = useCallback(async (engine: Engine) => {
        await loadFull(engine);
    }, []);
    

    return (
        <div className="App">
            <Particles options={particlesOptions as ISourceOptions} init={particlesInit}/>
            {/* Page container */}
            <div id="top" className="page-container text-white h-">
                {/* Navbar */}
                <div className="nav-bar h-28 flex items-center justify-between" >
                    <a href="#top" className="w-auto inline-block">
                        <img className="h-16 aspect-square ml-20" src={ravenLogo} alt="" />
                    </a>
                    <div className="links h-full flex items-center space-x-32 mr-36 font-semibold">
                        <a href="">Home</a>
                        <a href="">Projects</a>
                        <a href="">Contact</a>
                    </div>                        
                </div>
                {/* Main page */}
                {/* Side arrow with scroll text */}
                <div className="flex fixed top-1/2 -left-12 -rotate-90 items-center space-x-4">
                    <img className="h-10 -rotate-90" src={arrow} alt=""/>
                    <span className="text-sm tracking-wider">SCROLL TO SEE MORE</span>
                </div>
                {/* Center content */}
                <div className="flex flex-col justify-center items-center">
                    {/* Portrait photo */}
                    <img className="w-48 h-auto block rounded-full aspect-square mt-44" src={profile} alt=""/>
                    
                    {/* Title banner */}
                    <div className="text-7xl w-4/6 max-w-screen-xl text-center leading-tight mt-14 mb-20 font-sans tracking-tighter font-semibold">
                        DATA SCIENTIST. NATURE LOVER. <br />
                        LIFE LONG LEARNER
                    </div>

                    {/* Resume button */}
                    <a style={{ border: "1px solid white" }} className="p-4 rounded-sm w-48 text-center text-md"
                    target="_blank" href={resume}>
                        <i className="fa-regular fa-file mr-2"></i> Resume
                    </a>

                    {/* Welcome paragraph */}
                    <Welcome/>

                    {/* Projects panel */}
                    <div className="max-w-max min-w-max">
                        <Projects/>
                    </div>

                </div>
            </div>
        </div>
    );
} 

export default App;
